$green: #00cc4e
$red: #e94545
$hover: #649cfb
$blue: #3f7de5
$black: #2c2c2c
$dark-gray: #424242
$gray: #6f6f6f
$stroke: #d9d9d9
$bg: #f5f5f7
$white: #fff
$font-family: "Onest", sans-serif

body
    background: $bg
    position: relative
.container
    padding: 0 48px
.btn
    &__blue
        background: $blue
        padding: 20px 32px
        border: none
        max-width: max-content
        width: 100%
        font-family: $font-family
        font-weight: 500
        font-size: 16px
        line-height: 1.25
        letter-spacing: 0.02em
        text-transform: uppercase
        color: $white
        transition: all .3s
        &:hover
            box-shadow: 0 25px 55px 0 rgba(93, 92, 96, 0.1), 0 100px 100px 0 rgba(93, 92, 96, 0.09), 0 225px 135px 0 rgba(93, 92, 96, 0.05), 0 400px 160px 0 rgba(93, 92, 96, 0.01)
            background: $hover
    &__wht
        border: 1px solid $blue
        padding: 20px 32px
        background: transparent
        max-width: max-content
        width: 100%
        font-family: $font-family
        font-weight: 500
        font-size: 16px
        line-height: 1.25
        letter-spacing: 0.02em
        text-transform: uppercase
        color: $dark-gray
        transition: all .3s
        &:hover
            background: $hover
            border: 1px solid $hover
            color: $white
.element-animation
    opacity: 0
    transition: all 1s
    transform: translateY(100%)
.element-show
    opacity: 1
    transform: translateY(0)
.input
    position: relative
    z-index: 1
    p
        font-family: $font-family
        font-weight: 400
        font-size: 18px
        line-height: 1.35
        color: $gray
        position: absolute
        left: 0
        top: 0
        transition: all .3s
        z-index: -1
        span
            color: $red
    input,
    textarea
        padding: 0 0 16px
        border: none
        background: transparent
        border-bottom: 1px solid $stroke
        width: 100%
        box-sizing: border-box  
        transition: all .3s
        font-family: $font-family
        font-weight: 400
        font-size: 18px
        line-height: 1.35
        color: $black
        &:hover
            border-bottom: 1px solid $hover
            &+p
                color: $black
        &:not(:placeholder-shown),
        &:focus
            border-bottom: 1px solid $blue
            &+p
                color: $gray
                transform: translateY(-100%)
                font-size: 12px
    textarea
        position: relative
        resize: vertical
        height: 40px
        &::-webkit-resizer
            background: url('../img/resize.svg') center no-repeat
            width: 24px
            height: 24px
            position: absolute
            bottom: 16px
            right: 0
.itc-select
    position: relative
    svg
        position: absolute
        top: 0
        right: 0
        width: 24px
        height: 24px
        transform: rotate(90deg)
        path
            transition: all .3s
            stroke: $dark-gray
    &__toggle
        padding: 0 0 16px
        border: none
        background: transparent
        border-bottom: 1px solid $stroke
        width: 100%
        box-sizing: border-box  
        transition: all .3s
        font-family: $font-family
        font-weight: 400
        font-size: 18px
        line-height: 1.35
        color: $black
        border-radius: 0
        font-style: normal
        &:hover
            border-bottom: 1px solid $hover
            color: $black
            &+svg
                path
                    stroke: $hover
        &::after
            content: none
    &__dropdown
        border-radius: 0
        border: 1px solid $stroke
    &__option
        padding: 10px 16px
        font-family: $font-family
        font-weight: 400
        font-size: 18px
        line-height: 1.35
        color: $dark-gray
        &::after
            content: none
        &_selected,
        &:hover
            background: $hover
            color: $white

.nav
    padding: 24px 0
    position: relative
    .container
        display: flex
        justify-content: flex-start
        align-items: center
    &__logo
        display: block
        max-width: 157px
        img
            width: 100%
    ul
        margin: 0 auto 0 48px
        display: flex
        justify-content: center
        align-items: center
        gap: 24px
        a
            font-family: $font-family
            font-weight: 500
            font-size: 16px
            line-height: 1.25
            letter-spacing: 0.02em
            text-transform: uppercase
            color: $dark-gray
            transition: all .3s
            &:hover
                color: $hover
    &__phone
        a
            font-family: $font-family
            font-weight: 600
            font-size: 24px
            line-height: 1.35
            text-transform: uppercase
            color: $blue
            transition: all .3s
            &:hover
                color: $hover
        p
            display: none
            margin: 4px 0 0
            font-family: $font-family
            font-weight: 400
            font-size: 18px
            line-height: 1.35
            color: $gray
    &__soc
        margin: 0 12px 0 34px
        display: grid
        grid-template-columns: 60px 60px 60px
        gap: 12px
        a
            display: block
            width: 100%
            height: 60px
            background: $white
            transition: all .3s
            display: flex
            align-items: center
            justify-content: center
            svg
                width: 100%
                path
                    transition: all .3s
            &:hover
                box-shadow: 0 25px 55px 0 rgba(93, 92, 96, 0.1), 0 100px 100px 0 rgba(93, 92, 96, 0.09), 0 225px 135px 0 rgba(93, 92, 96, 0.05), 0 400px 160px 0 rgba(93, 92, 96, 0.01)
                background: $hover
                svg
                    path
                        fill: white
                        stroke: none
                    g
                        path
                            stroke: white
                            fill: none

