        color: $blue
    &__price
        font-family: $font-family
        font-weight: 500
        font-size: 42px
        line-height: 1.1
        text-align: right
        color: $blue        
    &__sale
        font-family: $font-family
        font-weight: 400
        font-size: 18px
        line-height: 1.35
