        margin: 32px 0 0
        display: grid
        grid-template-columns: 1fr 1fr 1fr 1fr
        grid-template-rows: 1fr 1fr
        gap: 24px
    &__block
        background: $white
        padding: 32px
        display: flex
        flex-direction: column
        justify-content: space-between
        align-items: flex-start
    &__name
        font-family: $font-family
        font-weight: 400
        font-size: 20px
        line-height: 1.35
        color: $gray
    &__link,
    &__adress
        font-family: $font-family
        font-weight: 400
        font-size: 24px
        line-height: 1.35
        color: $dark-gray
    &__soc
        display: flex
        flex-wrap: wrap-reverse
        gap: 12px
        a
            display: flex
            justify-content: center
            align-items: center
            width: 60px
            height: 60px
            background: $bg
            transition: all .3s
            svg
                width: 32px
                path
                    transition: all .3s
            &:hover
                box-shadow: 0 25px 55px 0 rgba(93, 92, 96, 0.1), 0 100px 100px 0 rgba(93, 92, 96, 0.09), 0 225px 135px 0 rgba(93, 92, 96, 0.05), 0 400px 160px 0 rgba(93, 92, 96, 0.01)
                background: $hover
                svg
                    path
                        fill: white
                        stroke: none
                    g
                        path
                            stroke: white
                            fill: none
    &__req
        background: $white
        padding: 32px
        grid-column: 3/5
        grid-row: 1/3
        h4
            font-family: $font-family
            font-weight: 500
            font-size: 32px
            line-height: 1.1
            color: $dark-gray
            max-width: 610px
    &__form
        margin: 48px 0 0
        display: grid
        grid-template-columns: 100% 
        gap: 24px
        .btn__blue
            max-width: 100%
    &__row
        display: grid
        grid-template-columns: 1fr 1fr
        gap: 24px
    &__check
        position: relative
        display: grid
        grid-template-columns: 16px 1fr
        gap: 8px
        align-items: center
        p
            font-family: $font-family
            font-weight: 400
            font-size: 14px
            line-height: 1.35
            color: $gray
        span
            border: 1px solid $stroke
            display: block
            width: 16px
            height: 16px
            transition: all .3s
            position: relative
            &::before
                content: ''
                display: block
                inset: 0
                transition: all .3s
                position: absolute
                width: 100%
                height: 100%
                opacity: 0
                background: url('../img/check.svg') center no-repeat
        input
            position: absolute
            inset: 0
            opacity: 0
            z-index: 5
            width: 100%
            height: 100%
            &:checked
                &+span
                    border: 1px solid $blue
                    &::before
                        opacity: 1
            &:hover
                &+span
                    border: 1px solid $blue

